.farming-timer-container{
    width: auto;
    margin: auto;
    height: 100%;
    background-color: #f3f3f3;
    text-align: center;
}
.top-icons{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
}

.profile{
    display: flex;
    flex-direction: row;
}

.upgrade-translate{
    display: flex;
    flex-direction: row;
    align-items: center;
}

.upgrade-button{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 0.75rem;
    border-radius: 1em;
    border: 1px solid blue;
    background-color: blue;
    color: white;
    padding: 0.5rem 0.75rem;
    cursor: pointer;
}

.welcome-dawg-image{
    width: 175px;
}

.farming-info{
    display: flex;
    justify-content: space-between;
    font-size: 22px;
    font-weight: bold;
    padding: 3rem;
}

@keyframes fadeOut {
    0% {
      opacity: 1;
    }
    100% {
      opacity: 0;
    }
  }
  
  .loading-animation {
    animation: fadeOut 2s forwards;
  }
  
  .game-loading-image {
    width: 100%;
    max-width: 200px;
  }
  